import { calculateAnswer, isSameArray } from './util';

const placeNameToIndex = placeName => {
    var wholePlaceNames = [
        'ones',
        'tens',
        'hundreds',
        'thousands',
        'ten-thousands',
        'hundred-thousands',
        'millions',
        'ten-millions',
        'hundred-millions',
        'billions',
        'ten-billions',
        'hundred-billions',
        'trillions',
        'ten-trillions',
        'hundred-trillions',
        'quadrillions'
    ];
    var DeciPlaceNames = ['tenths', 'hundredths', 'thousandths', 'ten-thousandths', 'hundred-thousandths'];
    var wholePlcaeIndex = wholePlaceNames.indexOf(placeName);
    if (wholePlcaeIndex !== -1) {
        return wholePlcaeIndex;
    } else {
        return (DeciPlaceNames.indexOf(placeName) + 1) * -1;
    }
};

const getPlaceDigit = (num, place) => {
    var splitNum = num.toString().split('.');
    var wholePart = splitNum[0].split('');
    var deciPart;
    var placeNum;
    wholePart = wholePart.map(function (el) {
        return Number(el);
    });

    //소수일 때, deciPart 추가
    if (splitNum[1]) {
        deciPart = splitNum[1].split('');
        deciPart = deciPart.map(function (el) {
            return Number(el);
        });
    }

    if (place === 'array') {
        if (deciPart) {
            placeNum = [wholePart, deciPart];
        } else {
            placeNum = wholePart;
        }
    } else {
        var placeIndex = placeNameToIndex(place);
        if (placeIndex >= 0) {
            wholePart.reverse();
            placeNum = wholePart[placeIndex];
        } else {
            placeNum = deciPart[placeIndex * -1 - 1];
        }
    }

    return placeNum;
};

const getRegroupTypeArray = (num, sign) => {
    var type = [];
    var check = [];
    var maxLen = Math.max(_getLength(num[0]), _getLength(num[1]));

    if (sign === '+') {
        for (var i = 0; i < maxLen; i++) {
            check.push(0); //초기값 세팅
        }
        num.forEach(function (el) {
            for (var j = 0; j < check.length; j++) {
                check[j] = check[j] + (_getLength(el) > j ? getPlaceDigit(el, indexToPlaceName(j)) : 0);
            }
        });
        check.forEach(function (el, i) {
            if (i > 0) {
                check[i] = el + Math.floor(check[i - 1] / 10);
            }
            type[i] = check[i] >= 10 ? 'R' : 'NR';
        });
    } else if (sign === '-') {
        num.forEach(function (el, i) {
            for (var j = 0; j < maxLen; j++) {
                check[j] = i > 0 ? check[j] - (_getLength(el) > j ? getPlaceDigit(el, indexToPlaceName(j)) : 0) : getPlaceDigit(el, indexToPlaceName(j));
            }
        });
        check.forEach(function (el, i) {
            if (i > 0) {
                check[i] = el + Math.floor(check[i - 1] / 10);
            }
            if (i < check.length - 1) {
                type[i + 1] = check[i] < 0 ? 'R' : 'NR';
            }
        });
        type[0] = 'NR';
    } else if (sign === '*') {
        // 두 수일 때만 가능
        var regroupValue = [];
        getPlaceDigit(num[0], 'array')
            .reverse()
            .forEach(function (el, i) {
                regroupValue[i] = 0;
                var _product = el * getPlaceDigit(num[1], 'ones');
                var _regroupValue = i > 0 ? regroupValue[i - 1] : 0;
                if (_product + _regroupValue >= 10) {
                    type[i] = _product < 10 ? 'R-add' : 'R';
                    regroupValue[i] = getPlaceDigit(_product + _regroupValue, 'tens');
                } else {
                    type[i] = 'NR';
                }
            });
    }

    function _getLength(n) {
        return n.toString().length;
    }

    function indexToPlaceName(index, singular) {
        var placeName = [
            'ones',
            'tens',
            'hundreds',
            'thousands',
            'ten-thousands',
            'hundred-thousands',
            'millions',
            'ten-millions',
            'hundred-millions',
            'billions',
            'ten-billions',
            'hundred-billions',
            'trillions',
            'ten-trillions',
            'hundred-trillions',
            'quadrillions'
        ];
        var singularPlaceName = [
            'one',
            'ten',
            'hundred',
            'thousand',
            'ten-thousand',
            'hundred-thousand',
            'million',
            'ten-million',
            'hundred-million',
            'billion',
            'ten-billion',
            'hundred-billion',
            'trillion',
            'ten-trillion',
            'hundred-trillion',
            'quadrillion'
        ];
        var myName;
        if (index >= 0) {
            myName = placeName[index];
            if (singular) {
                myName = singularPlaceName[index];
            }
        }
        return myName;
    }

    return type;
};

const isRegroupType = (num, sign, type) => {
    var bool = false;
    var regroupArray = getRegroupTypeArray(num, sign);
    var r = 0;
    regroupArray.forEach(el => {
        if (el.charAt(0) === 'R') r += 1;
    });

    switch (type) {
        case 'NR':
            if (r === 0) bool = true;
            break;
        case 'R':
            if (r !== 0) bool = true;
            break;
        case 'R1':
            if (r === 1) bool = true;
            break;
        case 'R2':
            if (r === 2) bool = true;
            break;
        case 'R3':
            if (r === 3) bool = true;
            break;
        case 'R1-ones':
            if (r === 1 && regroupArray[0] === 'R') bool = true;
            break;
        case 'R1-tens':
            if (r === 1 && regroupArray[1] === 'R') bool = true;
            break;
        case 'R1-hundreds':
            if (r === 1 && regroupArray[2] === 'R') bool = true;
            break;
        case 'R1-thousands':
            if (r === 1 && regroupArray[3] === 'R') bool = true;
            break;
        case 'R2-conti':
            if (r === 2 && isConti(2)) bool = true;
            break;
        case 'R2-more':
            if (r >= 2) bool = true;
            break;
        case 'R3-conti':
            if (r === 3 && isConti(3)) bool = true;
            break;
        case 'R3-more':
            if (r >= 3) bool = true;
            break;
        case 'R-add': //곱셈에서 더해서 받아올림
            if (regroupArray.indexOf('R-add') !== -1) bool = true;
            break;
    }

    function isConti(n) {
        var bool = false;
        var s = regroupArray.indexOf('R');
        var c = 0;
        regroupArray.forEach(function (el, i) {
            if (el === 'R' && i === s) {
                s += 1;
                c += 1;
            }
        });
        if (c === n) {
            bool = true;
        }
        return bool;
    }

    return bool;
};

export const D1Generate = function ({ num, condition, log = false, sample = undefined, oldCheck = false }) {
    var oldNum = [];
    var olderNum = [];
    var genNum;
    var n = 0;
    var countBack = [];
    var trueCount = [];
    var initCheck = true;
    var self = this;
    let k;

    this.gen = function () {
        var limit = 0; //무한루프 방지
        do {
            genNum = [];
            for (var i = 0; i < num.length; i++) {
                genNum[i] = 0;
                for (var j = 0; j < num[i].length; j++) {
                    genNum[i] =
                        genNum[i] + (Math.floor(Math.random() * (num[i][j][1] - num[i][j][0] + 1)) + num[i][j][0]) * Math.pow(10, num[i].length - j - 1);
                }
            }
            if (log) console.log(genNum);
            limit += 1;
        } while (!isAllTrue(genNum, condition, oldNum, olderNum) && limit < 1000);

        if (limit >= 1000) {
            this.ans = undefined;
            console.log('You can not generate numbers!');
            this.num = sample;
            if (this.num && this.sign) {
                this.ans = calculateAnswer(this.num, this.sign);
            }
        } else {
            olderNum = oldNum;
            oldNum = genNum;
            if (genNum.length === 1) this.num = genNum[0];
            else this.num = genNum;
            n += 1;
            countBack = [];
        }

        function isAllTrue(genNum, condition, oldNum, olderNum) {
            var bool = false;
            if (oldCheck === false) {
                if (isConditionTrue(genNum, condition)) {
                    bool = true;
                }
            } else {
                if (isConditionTrue(genNum, condition) && !isSameArray(genNum, oldNum) && !isSameArray(genNum, olderNum)) {
                    bool = true;
                }
            }
            return bool;
        }
    };

    function isConditionTrue(num, obj) {
        var bool = true;
        if (obj) {
            var lastCheck = Object.keys(obj).length;
            var m = 0;
            k = 0;
            for (var key in obj) {
                if (n === 0) {
                    trueCount[m] = 0;
                }
                if (!isProbabilityTrue(checkCondition(key, obj[key], num), obj[key].probability, n, m, lastCheck)) {
                    bool = false;
                }
                m += 1;
            }
        }
        return bool;
    }
    //조건 체크 함수
    function checkCondition(key, value, num) {
        var bool = true;
        var myKey = findKey(key);
        switch (myKey) {
            case 'add':
            case 'subtract':
            case 'multiply':
                var sign = {
                    add: '+',
                    subtract: '-',
                    multiply: '*'
                }[myKey];
                var answer = value.answer || value.sum || value.difference || value.product;
                self.ans = calculateAnswer(num, sign);
                self.sign = sign;
                //정답 범위 조건
                if (answer && !(answer[0] <= self.ans && self.ans <= answer[1])) return false;
                //받아올림 조건
                if (value.regroup && !isRegroupType(num, sign, value.regroup)) return false;
                //연산결과 특정 자릿값 범위 지정 조건
                if (value.place && !checkPlace(self.ans, value.place)) return false;
                break;
            case 'divide':
                self.ans = calculateAnswer(num, '/');
                self.sign = '/';
                //몫 범위 조건
                if (value.quotient) {
                    if (!(value.quotient[0] <= self.ans.q && self.ans.q <= value.quotient[1])) return false;
                }
                //나머지 범위 조건
                if (value.remainder) {
                    if (!(value.remainder[0] <= self.ans.r && self.ans.r <= value.remainder[1])) return false;
                }
                //몫에 0 포함 조건
                if (value.zeroQ !== undefined) {
                    var _zeroPlace = ['ones', 'tens', 'hundreds', 'thousands'].indexOf(value.zeroQ);
                    if (_zeroPlace !== -1) {
                        if (
                            !self.ans.q
                                .toString()
                                .split('')
                                .reverse()
                                .every((el, i) => (i === _zeroPlace && el === '0') || (i !== _zeroPlace && el !== '0'))
                        ) {
                            return false;
                        }
                    } else {
                        // true or false
                        if (
                            value.zeroQ !==
                            self.ans.q
                                .toString()
                                .split('')
                                .some(el => el === '0')
                        )
                            return false;
                    }
                }
                break;
        }

        function findKey(key) {
            var keyList = ['add', 'subtract', 'multiply', 'divide'];
            var myKey;
            keyList.forEach(function (el) {
                if (key.indexOf(el) !== -1) {
                    myKey = el;
                }
            });
            return myKey;
        }
        return bool;
    }

    function isProbabilityTrue(condition, probability, n, m, lastCheck) {
        var bool = false;
        if (initCheck === n) {
            if (countBack.length !== 0) {
                countBack.forEach(function (el) {
                    trueCount[el] -= 1;
                });
                countBack = [];
            }
            initCheck = n - 1;
        }
        if (condition) {
            trueCount[m] += 1;
            countBack.push(m);
        }
        if ((trueCount[m] / (n + 1)) * 100 >= (probability || 100)) {
            console.warn({ trueCount, m, n, 누적확률: (trueCount[m] / (n + 1)) * 100 });
            bool = true;
            k += 1;
        }
        if (m === lastCheck - 1) {
            initCheck = n;
        }
        if (!bool) {
            if (countBack.length !== 0) {
                countBack.forEach(function (el) {
                    trueCount[el] -= 1;
                });
                countBack.splice(countBack.length - 1, 1);
            }
        }
        if (log) console.log('조건' + m + ':' + condition, '+ 누적확률:' + (trueCount[m] / (n + 1)) * 100, '--------> 최종:' + bool);
        return bool;
    }

    function checkPlace(ans, placeObj) {
        var reAnsArr = getPlaceDigit(ans, 'array').reverse();
        var index;
        var value;
        var bool = true;
        for (var key in placeObj) {
            index = placeNameToIndex(key);
            value = placeObj[key];
            if (!(value[0] <= reAnsArr[index] && reAnsArr[index] <= value[1])) {
                bool = false;
            }
        }
        return bool;
    }
};
