import axios from 'axios';

const responseButtonElement = [
    {
        src: NumBtn,
        key: 'NumBtn',
        type: 'svg'
    },
    {
        src: NumBtnPress,
        key: 'NumBtnPress',
        type: 'svg'
    }
];
const responseButtonBg = [
    {
        src: BgCalculate,
        key: 'BgCalculate',
        type: 'svg'
    }, {
        src: BgHandWrite,
        key: 'BgHandWrite',
        type: 'svg'
    }
];
const responseShowBg = [
    {
        src: CalShow,
        key: 'CalShow',
        type: 'svg'
    }
];
const responseButtonSymbol = [
    {
        src: ResetBtn,
        key: 'ResetBtn',
        type: 'svg'
    },
    {
        src: ResetBtnPress,
        key: 'ResetBtnPress',
        type: 'svg'
    },
    {
        src: OkBtn,
        key: 'OkBtn',
        type: 'svg'
    },
    {
        src: OkBtnPress,
        key: 'OkBtnPress',
        type: 'svg'
    }
];

//버튼 배경과 엘리먼트 합치기
const migrateImage = function ({ g, back, el, matrix }) {
    const subGroup = g.g();
    const backGround = back.use().appendTo(subGroup).transform(matrix);
    if (el != null) {
        const backBox = backGround.getBBox();
        el.use()
            .appendTo(subGroup)
            .transform('t' + [backBox.cx, backBox.cy])
            .center();
    }
    return subGroup;
};

export const D1CalculateOK = function ({ group, limitLen = 10, callback }) {
    const g = group.g().attr({
        visibility: 'hidden'
    });
    let expression = [];
    let result;
    const calBody = { cx: 565, cy: 250 }; // 계산기 위치만 조정
    const calScreen = { cx: calBody.cx, cy: calBody.cy - 110 }; //계산기 기준 중앙에서 110만큼 위로 올림
    const btnLocate = { x: 488, y: 185 };
    const margin = { x: 55, y: 50 };
    const scaleX = 1;
    var keyImgs = {
        normal: [loadImage(responseButtonElement[0]), loadImage(responseButtonElement[1])],
        bg: loadImage(responseButtonBg[0]),
        showBg: loadImage(responseShowBg[0]),
        reset: [loadImage(responseButtonSymbol[0]), loadImage(responseButtonSymbol[1])],
        enter: [loadImage(responseButtonSymbol[2]), loadImage(responseButtonSymbol[3])]
    };

    const btn = [
        ['7', '8', '9'],
        ['4', '5', '6'],
        ['1', '2', '3'],
        ['reset', '0', 'enter']
    ];

    //기본 외형 그리기
    keyImgs.bg
        .use()
        .transform('t' + [calBody.cx, calBody.cy])
        .appendTo(g)
        .center();
    const showEl = keyImgs.showBg
        .use()
        .transform('t' + [calScreen.cx, calScreen.cy])
        .appendTo(g)
        .center();
    const { w: showElW, x2: showElX2, cy: showElCy } = showEl.getBBox();

    //버튼
    const display = g.g();
    const buttons = btn;
    const numFs = 25;
    for (var i = 0; i < btn.length; i++) {
        for (let j = 0; j < btn[i].length; j++) {
            const _x = btnLocate.x + j * margin.x;
            const _y = btnLocate.y + i * margin.y;
            const _value = btn[i][j];
            buttons[i][j] = g.g();
            if (_value === 'reset' || _value === 'enter') {
                for (let k = 0; k < 2; k++) {
                    migrateImage({
                        g: buttons[i][j],
                        back: keyImgs['normal'][k],
                        el: keyImgs[_value][k],
                        matrix: Snap.matrix(scaleX, 0, 0, 1, _x, _y + [0, 1][k])
                    })
                        .attr({
                            opacity: [1, 0][k]
                        })
                        .addClass('change-opacity');
                }
            } else {
                const numberBtn = keyImgs.normal[0]
                    .use()
                    .transform('matrix(' + scaleX + ' 0 0 1 ' + _x + ' ' + _y + ')')
                    .appendTo(buttons[i][j])
                    .addClass('change-opacity');
                keyImgs.normal[1]
                    .use()
                    .transform('matrix(' + scaleX + ' 0 0 1 ' + _x + ' ' + (_y + 1) + ')')
                    .attr({
                        opacity: 0
                    })
                    .appendTo(buttons[i][j])
                    .addClass('change-opacity');
                const { w, h } = numberBtn.getBBox();
                //버튼 텍스트
                buttons[i][j]
                    .text(_x + w / 2, _y + h / 2 + numFs / 2, _value)
                    .addClass('dn53 f37')
                    .attr({
                        'pointer-events': 'none',
                        fontSize: numFs,
                        textAnchor: 'middle'
                    });
            }
            buttons[i][j].data('val', _value).pressEvent(changeButton);
        }
    }

    function changeButton(bool) {
        if (bool) {
            // ESLS.DATA.commonSounds.btn[1].play();
            changeBtn(this, 0);
        } else {
            changeBtn(this, 1);
            buttonCallback.call(this);
        }

        function changeBtn(button, status) {
            const transformYData = [1, 0];
            // const classData = ['#828688', '#1C7C6A'];
            const classData = ['f37', 'f34'];
            button.selectAll('.change-opacity')[status].attr({
                opacity: 0
            });
            button.selectAll('.change-opacity')[1 - status].attr({
                opacity: 1
            });
            if (button.select('text')) {
                button
                    .select('text')
                    .transform('t' + [0, transformYData[status]])
                    .removeClass(classData[status])
                    .addClass(classData[1 - status]);
            }
        }
    }

    function buttonCallback(key, val) {
        let data = key ? val : this.data('val');
        let thisData;
        if (data !== undefined && data !== '') {
            switch (data) {
                case 'enter':
                    enter();
                    break;
                case 'reset':
                    expression = [];
                    showResult(expression);
                    break;
                default:
                    expression.push(data);
                    showResult(expression);
                    break;
            }
        }

        function enter() {
            if (expression.length !== 0) {
                thisData = {
                    data: () => expression.join('')
                };
                g.attr('pointerEvents', 'none');
                callback.call(thisData);
            }
        }
    }

    function showResult(exp) {
        // keyboardJS.pause(); //키보드 비활성(동시타 방지)
        // 길이제한
        if (exp.length > limitLen) {
            console.log("Expression's length is over " + limitLen + '!');
            exp.pop();
        }
        //입력 값이 0인 상태
        if (exp.length > 1 && exp[0] === '0') {
            console.log('zero state');
            exp.shift();
        }

        let numStr = exp.join('');
        const cx = showElX2 - 5;
        const cy = showElCy;

        let scale = 1;

        //result초기화 후 새로 작성
        if (result) result.remove();
        result = display
            .text(cx, cy, numStr)
            .addClass('dn53 f34')
            .attr({
                fontSize: numFs,
                textAnchor: 'end'
            })
            .center();
        //scale 조정
        const { w: resultW } = result.getBBox();
        if (resultW > showElW - 20) scale = (showElW - 20) / resultW;
        display.transform('s' + scale + ' ' + [cx, cy]);
        display.data('scale', scale);
        display.data('cxcy', [cx, cy]);
        // keyboardJS.resume(); //키보드 활성
    }

    //키보드 이벤트 활성
    // this.bindKeyboard = function () {
    //     MATH.BUTTON.keyboard(buttonCallback);
    // };
    this.showBg = showEl;
    this.show = () => {
        g.attr('visibility', 'visible');
        // if (!ESLS.SYSTEM.isMobile()) {
        //     this.bindKeyboard();
        // }
    };
    this.remove = () => {
        g.remove();
        // if (!ESLS.SYSTEM.isMobile()) {
        //     keyboardJS.reset();
        // }
    };
    this.stop = () => {
        g.attr('pointerEvents', 'none');
    };
    this.reStart = async () => {
        expression = [];
        showResult(expression);        
        g.attr('pointerEvents', 'auto');
    };
};

export const D1HandWriteOK = function ({ group, limitLen = 10, callback, handWriteRecognitionLevel }) {
    const pathColorNum = '04';
    const pathWidth = 6;
    const g = group.g().attr({
        visibility: 'hidden'
    });
    const pathAttr = {
        class: 'fno s' + pathColorNum,
        strokeWidth: pathWidth,
        strokeLinecap: 'round',
        strokeLinejoin: 'round'
    };
    let expression = [];
    let padGroup;
    let display;
    let output = '';
    const calBody = { cx: 565, cy: 250 }; // 계산기 위치만 조정
    const calScreen = { cx: calBody.cx, cy: calBody.cy - 110 }; //계산기 기준 중앙에서 110만큼 위로 올림
    const padArea = [calBody.cx - 94, calBody.cy - 70, calBody.cx + 94, calBody.cy + 100]; //계산기 이미지의 옆선 12px;
    const buttonInfo = [
        {
            value: 'reset',
            xy: [480, 355]
        },
        {
            value: 'enter',
            xy: [610, 355]
        }
    ];
    const buttons = [];
    const keyImgs = {
        normal: [loadImage(responseButtonElement[0]), loadImage(responseButtonElement[1])],
        bg: loadImage(responseButtonBg[1]),
        showBg: loadImage(responseShowBg[0]),
        reset: [loadImage(responseButtonSymbol[0]), loadImage(responseButtonSymbol[1])],
        enter: [loadImage(responseButtonSymbol[2]), loadImage(responseButtonSymbol[3])]
    };

    // 배경
    keyImgs.bg
        .use()
        .appendTo(g)
        .transform('t' + [calBody.cx, calBody.cy])
        .center();
    const showEl = keyImgs.showBg
        .use()
        .transform('t' + [calScreen.cx, calScreen.cy])
        .appendTo(g)
        .center();
    const { w: showElW, x2: showElX2, cy: showElCy } = showEl.getBBox();
    const numFs = 25;

    let result;
    //버튼
    buttonInfo.forEach(function (el, i) {
        buttons[i] = g.g();
        for (let j = 0; j < 2; j++) {
            migrateImage({
                g: buttons[i],
                back: keyImgs['normal'][j],
                el: keyImgs[el.value][j],
                matrix: 't' + [el.xy[0], el.xy[1] + j * 1]
            }).attr({
                opacity: 1 - j
            });
        }
        buttons[i].data('val', el.value).pressEvent(changeButton);
    });

    async function makePad(array) {
        if (padGroup) padGroup.remove();
        padGroup = new D1GetString({
            g: group,
            area: padArea,
            strokeNum: pathColorNum,
            strokeWidth: pathWidth,
            endCallback: padCallback
        });
        padGroup.start();
        padGroup.pad.attr({
            opacity: 1,
            'fill-opacity': 0,
            strokeWidth: 1,
            strokeDasharray: '5 5',
            strokeLinecap: 'round',
            stroke: 'green'
        });
        const recognitionPaths = [];
        array.forEach(function (el) {
            padGroup.pathG.path(el.str).attr(pathAttr);
            recognitionPaths.push(el.recognitionPath);
        });
        if (recognitionPaths.length === 0) {
            showResult('');
        } else {
            recognize(recognitionPaths);
        }
    }

    function padCallback(path, pathStr) {
        // console.log(pathStr, 'pathStr');
        expression.push({
            recognitionPath: pathStr[pathStr.length - 1],
            str: pathStr[pathStr.length - 1]
        });
        makePad(expression);
    }

    async function recognize(paths) {
        // console.log(paths, padArea);
        // console.log('data none');
        disable();
        const { totalMatrix } = g.transform();
        const scaledArea = matrixArea(padArea, totalMatrix);
        const { val, error } = await handWriteRecognition({ points: getHandWritePoints(paths, scaledArea), handWriteRecognitionLevel });

        if (error) {
            // 숫자인식 에러일 때
            expression = [];
            makePad(expression);
        } else {
            showResult(val);
        }
        enable();
    }

    function changeButton(bool) {
        if (bool) {
            // ESLS.DATA.commonSounds.btn[1].play();
            _changeBtn(this, 0);
        } else {
            _changeBtn(this, 1);
            buttonCallback.call(this);
        }

        function _changeBtn(button, status) {
            button.selectAll('g')[status].attr('opacity', 0);
            button.selectAll('g')[1 - status].attr('opacity', 1);
        }
    }

    function buttonCallback() {
        const data = this.data('val');
        let thisData;
        switch (data) {
            case 'enter':
                if (output !== '') {
                    thisData = {
                        data: function () {
                            return output;
                        },
                        path: function () {
                            return padGroup.pathG.selectAll('path');
                        }
                    };
                    stop();
                    callback.call(thisData);
                }
                break;
            case 'reset':
                expression = [];
                makePad(expression);
                break;
        }
    }

    function disable() {
        buttons.forEach(function (el) {
            el.attr({
                opacity: 0.5,
                'pointer-events': 'none'
            });
        });
        if (padGroup?.pad) padGroup.pad.attr('pointerEvents', 'none');
    }

    function enable() {
        buttons.forEach(function (el) {
            el.attr({
                opacity: 1,
                'pointer-events': 'auto'
            });
        });
        if (padGroup?.pad) padGroup.pad.attr('pointerEvents', 'auto');
    }

    async function showResult(exp) {
        if (exp.length > limitLen) {
            console.log("Expression's length is over " + limitLen + '!');
            output = exp.slice(0, exp.length - 1);
            padGroup.pathG.selectAll('path')[limitLen].remove();
            expression.pop();
            console.log('output', output);
        } else {
            output = exp;
        }
        const cx = showElX2 - 5;
        const cy = showElCy;
        let scale = 1;
        if (display) display.remove();
        display = g.g();
        console.log(output);
        result = display
            .text(cx, cy, output)
            .addClass('dn53 f34')
            .attr({
                fontSize: numFs,
                textAnchor: 'end'
            })
            .center();

        //스케일 조정
        const { w: resultW } = result.getBBox();
        if (resultW > showElW - 20) scale = (showElW - 20) / resultW;
        display.transform('s' + scale + ' ' + [cx, cy]);
    }

    async function handWriteRecognition({ points: paths, handWriteRecognitionLevel }) {
        let val = '';
        let error = false;
        const postData = {
            formula: { path: paths.join() },
            visualize: true,
            level: handWriteRecognitionLevel,
            model_name: 'final' //'model_03_06_epoch_4'
        };

        // L로 시작하는 path는 M으로 강제 변환
        if (postData.formula.path.charAt(0) === 'L') {
            postData.formula.path = 'M' + postData.formula.path.substring(1);
        }

        try {
            const result = await axios.post('https://ml.esls.io:41261/predict', postData, { timeout: 10000 });
            val = removeLatexBeginEnd(result.data[0].formula);

            return { val, error };
        } catch (e) {
            val = '';
            error = true;
            if (!e.response) {
                alert('응답이 없습니다.\n잠시 후 다시 시도해 주세요!');
                return { error: true };
            }

            const { status } = e.response;
            // 422: path error, 426: level error(없는 레벨 들어갔을 때), 420: general error, 404: Given model does not exist
            if ([422, 426, 420, 404].includes(status)) {
                alert('손글씨를 인식할 수 없습니다.');
                return { val, error };
            }
            // 423, 424: 수식일 확률이 낮거나 아닐 경우(낙서), 425: formula syntax error, 427: 해당 level에 없는 기호 포함
            if ([423, 424, 425, 427].includes(status)) {                
                alert('글씨를 바르게 다시 써 보세요.');
                return { val, error };
            }

            alert('손글씨를 인식할 수 없습니다.\n잠시 후 다시 시도해 주세요!');
            return { val, error };
        }

        function removeLatexBeginEnd(latex) {
            var tex = latex;
            tex = tex.replace('\\begin{equation}', '');
            tex = tex.replace('\\end{equation}', '');
            tex = tex.replace(/\\{/g, '\\left\\{');
            tex = tex.replace(/\\}/g, '\\right\\}');
            tex = tex.replace(/\(/g, '\\left(');
            tex = tex.replace(/\)/g, '\\right)');
            tex = tex.replace(/[\\]times/g, '*');
            tex = tex.replace(/[\\]div/g, '÷');
            return tex;
        }
    }

    function matrixArea(area, matrix) {
        var newArea = [];
        for (var i = 0; i < 2; i++) {
            newArea[i * 2] = matrix.x(area[i * 2], area[i * 2 + 1]);
            newArea[i * 2 + 1] = matrix.y(area[i * 2], area[i * 2 + 1]);
        }
        return newArea;
    }

    function getHandWritePoints(pathStrArray, pathArea) {
        var paths = [];
        var boxX = pathArea[0];
        var boxY = pathArea[1];
        var boxW = pathArea[2] - pathArea[0];
        var boxH = pathArea[3] - pathArea[1];
        var gapX = 0;
        var gapY = 0;
        // pathArea의 가로가 750, 세로가 350보다 작을 때 가운데로 이동 (2020.06.02, 정훈)
        if (boxW < 750) {
            gapX = (800 - boxW) / 2;
        }
        if (boxH < 350) {
            gapY = (400 - boxH) / 2;
        }
        // var boxS = 280 / (boxW > boxH ? boxW : boxH);
        // var boxR = Math.min(600 / boxW, 280 / boxH); // #TODO 조정 필요

        pathStrArray.forEach(function (el, i) {
            paths[i] = '';
            Snap.parsePathString(el).forEach(function (el2) {
                // paths[i] = paths[i] + el2[0] + _reduce((el2[1] - boxX + 100) * boxR) + ',' + _reduce((el2[2] - boxY + 50) * boxR); // #TODO 조정 필요
                paths[i] = paths[i] + el2[0] + _reduce(el2[1] - boxX + gapX) + ',' + _reduce(el2[2] - boxY + gapY); // 2020.05.26
            });
        });

        function _reduce(number) {
            //소수점 3자리로 만들기
            return Math.round(number * 1e3) / 1e3;
        }

        return paths; //Sigmoidal 수식 인식 반영 2018.12.18 Daniel
    }

    this.showBg = showEl;

    this.show = () => {
        g.attr({
            visibility: 'visible'
        });
        makePad(expression);
    };
    this.remove = () => {
        g.remove();
        if (padGroup) padGroup.remove();
    };
    this.stop = () => {
        stop();
    };
    this.reStart = () => {
        enable();
        expression = [];
        showResult('');
        makePad(expression);
    };
};
