import Snap from 'sol-common/snap';
import { getSVGPoint } from 'sol-common/util';

const convertClass = c => {
    let color;
    switch (c) {
        case '02':
            return (color = '#ed6276');
        case '03':
            return (color = '#f7c541');
        case '04':
            return (color = '#85bc4a');
        case '05':
            return (color = '#e885b4');
        case '06':
            return (color = '#59c3ff');
        case '07':
            return (color = '#f48134');
        case '09':
            return (color = '#9467b9');
        case '12':
            return (color = '#508446');
        case '13':
            return (color = '#20a1e2');
        case '14':
            return (color = '#FFFFFF');
        case '34':
            return (color = '#80f1da');
        case '85':
            return (color = '#000000');
        default:
            return (color = '#000000');
    }
};

export const D1GetString = function ({ g, area, strokeNum = '85', strokeWidth = 10, startCallback, moveCallback, endCallback }) {
    const colorG = g.g().attr('visibility', 'hidden');
    const pathG = colorG.g();
    const writeArea = colorG
        .rect(area[0], area[1], area[2] - area[0], area[3] - area[1])
        .addClass('f04')
        .attr({
            opacity: 0,
            pointerEvents: 'none'
        });
    const pathAttr = {
        fill: 'none',
        stroke: convertClass(strokeNum),
        strokeWidth: strokeWidth,
        pointerEvents: 'none',
        strokeLinecap: 'round',
        strokeLinejoin: 'round'
    };
    let pathStr = [];
    let path = [];
    let index = 0;
    let isStart = false;
    const bbox = writeArea.getTBox();

    const drawPath = (command, event) => {
        const { clientX, clientY } = event;
        const { x, y } = getSVGPoint(clientX, clientY);
        if (Snap.path.isPointInsideBBox(bbox, x, y)) {
            pathStr[index] = pathStr[index] + command + x + ',' + y;
            if (pathStr[index][0] !== 'M') {
                console.warn('M', pathStr[index], '-----', 'M' + pathStr[index].slice(1) + pathStr[index]); // 버그 경과 보기 위한 로그
                pathStr[index] = 'M' + pathStr[index].slice(1) + pathStr[index];
            }
            path[index].attr('d', pathStr[index]);
        }
    };
    const startFunction = (x, y, event) => {
        isStart = true;
        event.preventDefault();
        if (startCallback) startCallback(event);
        pathStr[index] = '';
        path[index] = pathG.path('').attr(pathAttr);
        // effectSound.play();
        drawPath('M', event);
        drawPath('L', event);
    };
    const moveFunction = (dx, dy, x, y, event) => {
        if (!isStart) return;
        event.preventDefault();
        if (moveCallback) moveCallback(event);
        drawPath('L', event);
    };
    const endFunction = () => {
        if (!isStart) return;
        // effectSound.stop();
        index += 1;
        if (endCallback) endCallback(path, pathStr);
        isStart = false;
    };
    writeArea.drag(moveFunction, startFunction, endFunction);

    this.g = g;
    this.pathG = pathG;
    this.pad = writeArea;

    this.start = () => {
        colorG.attr('visibility', 'visible');
        writeArea.attr('pointerEvents', 'auto');
    };
    this.stop = () => {
        colorG.attr('visibility', 'hidden');
        writeArea.attr('pointerEvents', 'none');
    };
    this.remove = () => {
        colorG.remove();
    };

    return this;
};
