import { SOL } from 'sol-common/util';
import D1OPED from '../components/D1P015OPED';
import { D1Calculate } from '../components/D1Calculate';
import D1feedback from '../components/D1feedback';
import { wait } from '../utils/util';
import { D1Generate } from '../utils/D1Generate';
import Timer from '../components/D1Timer';

export default function DM000012({ canvas = SOL.SVG, ...p }) {
    const g = canvas;
    const callback = p.callback;

    const OPEDG = g.g().addClass('D1-opening-ending');
    const CalG = g.g().addClass('D1-calculate');
    const timerG = g.g().addClass('D1-timer');

    let D1oped;
    let D1calculate;
    let generateCal;

    const D1generate = new D1Generate(p.generate);
    const timer = new Timer(timerG, { timeout: 998 });
    const drawOPED = () => D1OPED({ g: OPEDG, term: 125 });

    const drawCalculate = ({ num, sign, ...rest }) => {
        const CALG = CalG.g();

        if (sign === '-' && num[0] < num[1]) {
            const temp = num[0];
            num[0] = num[1];
            num[1] = temp;
        }

        const D1calculate = new D1Calculate({ group: CALG, cx: 230, cy: 270, callback: data => handler(data), num, sign, ...rest });

        const handler = async info => {
            const { userAns, answer } = info;
            const result = userAns === answer;
            if (result) await D1feedback({ el: CALG, bool: true });
            CALG.remove();
            const nextCal = generateCal.next();
            if (nextCal.done) {
                timer.pause();
                await wait(300);
                await D1oped.ending();
                await wait(300);
                callback();
            }
        };

        return D1calculate;
    };

    function* generateCalculate() {
        const { sign, type, handWrite, blankIndex = 2, repeatCount, fontSize } = p.calculate;
        for (let index = 0; index < repeatCount; index++) {
            D1generate.gen();
            D1calculate = drawCalculate({ sign, type, handWrite, blankIndex, fontSize, num: D1generate.num });
            D1calculate.start();
            yield;
        }
    }

    async function start() {
        timer.timerG.transform('t35, -50');
        D1oped = drawOPED();
        generateCal = generateCalculate();
        await D1oped.opening();
        timer.init();
        generateCal.next();
    }

    start();
}
