export const textNumber = function ({ g, x, y, string, attr}) {
    const splitNum = string.split('.');
    const texts = g.text(x, y, '');
    let _splitArray = splitNum[0].split('');
    let tspan;
    let n = 0;
    const dx = attr?.dx || 0;

    const isDecimal = (number) => number.toString().split('.').length === 2;

    if (isDecimal(string)) {
        _splitArray.push('.');
        _splitArray = _splitArray.concat(splitNum[1].split(''));
    } else if (string.toString().split('.').length > 2) {
        for (let k = 0; k < string.toString().split('.').length - 1; k++) {
            _splitArray.push('.');
            _splitArray = _splitArray.concat(splitNum[k + 1].split(''));
        }
    }

    tspan = texts.attr({
        'text': _splitArray
    }).selectAll('tspan');
    _splitArray.forEach(function (el, i) {
        if (el === '.') {
            tspan[i].attr({
                dx: dx
            });
            if (tspan[i + 1]) {
                tspan[i + 1].attr({
                    dx: dx
                });
            }
        } else if (attr && attr.placeId) {
            n++;
        }
    });
    return texts;
}