import { textNumber } from '../utils/D1Text';
import { D1CalculateOK, D1HandWriteOK } from '../utils/D1Response';
import { calculateAnswer } from '../utils/util';
import ScreenH from '../assets/images/calculate/screen_horizontal.svg';
import ScreenV from '../assets/images/calculate/screen_verical.svg';

import { loadImage } from 'sol-common';

const screenImage = [
    {
        src: ScreenH,
        key: 'ScreenH',
        type: 'svg'
    }, {
        src: ScreenV,
        key: 'ScreenV',
        type: 'svg'
}];

const questionMark = function ({ group, scale, x, y }) {
    const g = group.g().attr({
        transform: 'matrix(' + scale + ' 0 0 ' + scale + ' ' + x + ' ' + y + ')'
    });
    const qPath = g.path(
        'M-1.4 -14.3c0 0-6.8-0.7-6.4-11.6 0 0 0.8-9 7.7-16.8 0 0 5.3-5.9 6.1-7.4 0 0 3.6-4.3 1.2-8.7 0 0-2.8-5.4-8.2-4.6 0 0-5.1 0.2-9 6.6 0 0-3.8 9.3-12.3 4.5 0 0-5.3-3.5-2.8-10.7 0 0 4.8-14.2 18.9-17.8 0 0 7.6-2.3 16.2 1.1 0 0 15.1 5.2 15.6 21.9 0 0 1.1 10.3-8.2 19.3 0 0-6.1 6.3-8.3 10.8 0 0-1.4 4.2-1.9 6.9C7.3 -20.8 6.2 -13 -1.4 -14.3z'
    );
    const qCircle = g.circle(0, 0, 9);
    qPath.addClass('f42 q-mark');
    qCircle.addClass('f42');
    return g;
};


export const D1Calculate = function ({
    group,
    type ='h', 
    num,
    sign,
    cx = 200,
    cy = 250,
    handWrite = false,
    handWriteRecognitionLevel = '2',
    blankIndex = 2,
    fontSize = 20,
    callback
}) {
    const g = group.g().attr('visibility', 'hidden');
    const backG = g.g();
    const expG = g.g();
    const variables = {
        sign: '',
        answer: '',
        calArray: [],
        calArrayLen: num.length * 2 + 1,
        offset: 0,
        myAnswer: 0,
        answerLen: 0,
        circle: false,
        totalMatrix: g.transform().totalMatrix
    };
    const objects = {
        blank: {},
        q: {},
        ans: {},
        button: {}
    };
    const boxIndex = blankIndex * 2;
    const button = { handWrite, handWriteRecognitionLevel };
    const _isHandWriteMode = button.handWrite; //adaptive button 설정

    //배경 이미지
    loadImage(screenImage[type === 'h' ? 0 : 1])
        .use()
        .transform('t' + [cx, cy])
        .appendTo(backG)
        .center();

    const getNumberWidth = (num, fs) => num.toString().length * fs * 0.6;
    const getOffset = (num, fs) => {
        if (num === undefined) {
            return 0;
        } else {
            const numStr = num.toString();
            let offset;
            if (numStr.length >= 2 && numStr.charAt(0) === '1' && numStr.charAt(numStr.length - 1) !== '1') {
                offset = -((fs / 50) * 4);
            } else if (numStr.length >= 2 && numStr.charAt(numStr.length - 1) === '1' && numStr.charAt(0) !== '1') {
                offset = (fs / 50) * 4;
            } else {
                offset = 0;
            }
            return offset;
        }
    };

    const _setValues = function () {
        switch (sign) {
            case '*':
                variables.sign = '×';
                variables.answer = calculateAnswer(num, sign);
                break;
            case '/':
                variables.sign = '÷';
                variables.answer = calculateAnswer(num, sign).q;
                break;
            default:
                variables.sign = sign;
                variables.answer = calculateAnswer(num, sign);
                break;
        }

        for (let i = 0; i < variables.calArrayLen; i++) {
            variables.calArray[i] =
                i % 2 === 0
                    ? i === variables.calArrayLen - 1
                        ? variables.answer.toString()
                        : num[i / 2].toString()
                    : i === variables.calArrayLen - 2
                    ? '='
                    : variables.sign;
        }
    };

    const _viewH = function () { //가로셈
        variables.myAnswer = variables.calArray[boxIndex];
        variables.answerLen = getNumberWidth(variables.myAnswer, fontSize) + 8;
        variables.calArray[boxIndex] = 'box';
        variables.calArray.forEach(el => {
            const isSign = ['+', '-', '×', '÷', '='].includes(el);
            if (el !== 'box') {
                textNumber({
                    g: expG,
                    x: variables.offset + getOffset(el, fontSize),
                    y: fontSize,
                    string: el,
                    attr: {
                        dx: -8,
                        placeId: true
                    }
                })
                    .addClass('dn53 f34')
                    .attr('fontSize', fontSize);
            } else {
                objects.blank = expG.rect(variables.offset, 0, variables.answerLen, fontSize).addClass('fno s01').attr('stroke-width', fontSize * 0.05);
                objects.qMark = questionMark({
                    group: expG,
                    x: variables.answerLen / 2 + variables.offset + (isSign ? 5 : 0),
                    y: fontSize * 0.8,
                    scale: fontSize * 0.008
                }).attr('visibility', 'hidden');
                objects.ans = textNumber({
                    g: expG,
                    x: variables.answerLen / 2 + variables.offset + getOffset(variables.myAnswer, fontSize) + (isSign ? 5 : 0),
                    y: fontSize,
                    string: variables.myAnswer,
                    attr: {
                        dx: -8
                    }
                })
                    .addClass('dn53 f34')
                    .attr({
                        visibility: 'hidden',
                        fontSize: fontSize,
                        textAnchor: 'middle'
                    });
            }
            variables.offset = variables.offset + (isSign ? fontSize : el === 'box' ? variables.answerLen : getNumberWidth(el, fontSize));
        });
        self.width = variables.offset * variables.totalMatrix.a;
        self.height = fontSize * variables.totalMatrix.d;
    };

    const _viewV = function () { //세로셈
        variables.myAnswer = variables.calArray[boxIndex];
        variables.answerLen = getNumberWidth(variables.myAnswer, fontSize) + 8;
        variables.calArray[boxIndex] = 'box';
        const numWidth = num.concat(variables.answer).map((el) => getNumberWidth(el, fontSize));
        const maxWidth = Math.max.apply(null, numWidth);
        variables.calArray.forEach((el, i) => {
            const isSign = ['+','-','×','÷','='].includes(el);
            if (el !== 'box') {
                if (!isSign) {
                    textNumber({
                        g: expG,
                        x: maxWidth + fontSize,
                        y: i * fontSize + fontSize + variables.offset,
                        string: el,
                        attr: {
                            dx: -8,
                            placeId: true
                        }
                    }).addClass('dn53 f34')
                      .attr({
                          'fontSize':fontSize,
                          'textAnchor':'end'
                        });
                }
            } else {
                objects.blank = expG.rect(
                    maxWidth + fontSize * 1.25 - variables.answerLen, 
                    i * fontSize + variables.offset + fontSize * 0.05, 
                    variables.answerLen, 
                    fontSize).addClass('fno s01')
                             .attr('stroke-width', fontSize * 0.05);
                objects.qMark = questionMark({
                    group: expG,
                    x: (maxWidth + fontSize * 1.25 - variables.answerLen / 2),
                    y: i * fontSize + variables.offset + fontSize * 0.85,
                    scale: fontSize * 0.008
                }).attr('visibility', 'hidden');
                objects.ans = textNumber({
                    g: expG,
                    x: (maxWidth + fontSize * 1.25 - variables.answerLen / 2),
                    y: i * fontSize + fontSize + variables.offset + fontSize * 0.05,
                    string: variables.myAnswer,
                    attr: {
                        dx: -8
                    }
                }).addClass('dn53 f34')
                    .attr({
                    'visibility': 'hidden',
                    'fontSize': fontSize,
                });
            }
            if (el === '=') {
                expG.text(-2, i * fontSize + variables.offset - fontSize * 0.05, variables.sign).addClass('dn53 f34').attr('fontSize', fontSize); //기호 (부호)
                expG.line(0, i * fontSize + variables.offset + fontSize * 0.05, maxWidth + fontSize * 1.35, i * fontSize + variables.offset + fontSize * 0.05).addClass('s34'); //밑줄
            }
            variables.offset = variables.offset + (isSign ? (el === '=' ? -fontSize * 0.85 : -fontSize) : (el === 'box' ? fontSize * 0.15 : fontSize * 0.05));
        });
        self.width = (maxWidth + fontSize * 1.35) * variables.totalMatrix.a;
        self.height = ((variables.calArray.length) * fontSize + variables.offset - fontSize * 0.05) * variables.totalMatrix.d;
        console.log('w',self.width,'h',self.height);
    };
    
    const _makeButton = function () {
        button.group = group.g();
        button.callback = _callback;        
        if (_isHandWriteMode) {
            //손글씨 반응판
            objects.button = new D1HandWriteOK(button);
        } else {
            objects.button = new D1CalculateOK(button);
        }
        self.button = objects.button;
    };

    function _callback() {
        const userAns = this.data('val');
        const answer = variables.myAnswer;
        callback({ userAns, answer, objects });
    }

    const _transform = () => {
        expG.transform('t' + [cx, cy]).center();
    };
    const _start = () => {
        _setValues();
        if(type === 'h') _viewH()
        else if(type === 'v') _viewV()
        _transform();
        _makeButton();
    };

    this.g = g;

    this.show = () => {
        g.attr('visibility', 'visible');
    };

    this.start = () => {
        g.attr('visibility', 'visible');
        objects.qMark.attr('visibility', 'visible');
        objects.button.show();
    };
    _start();

    return this;
};
