import { loadImage } from 'sol-common';
import Snap from 'sol-common/snap';
import { convertToPathStr, wait, getSimultaneousEquationConstant } from '../utils/util';

import D1background from '../assets/images/P015/wall.svg';
import leftDoor from '../assets/images/P015/door_left.svg';
import rightDoor from '../assets/images/P015/door_right.svg';
import openingStart from '../assets/images/P015/star.svg';

export default function D1P015OPED({ g, term = 100 }) {
    const clipPath = g.clipPath();
    const leftDoorG = g.g().addClass('D1-left-door').attr({ clipPath });
    const rightDoorG = g.g().addClass('D1-right-door').attr({ clipPath });
    const starG = g.g().addClass('D1-star');
    const backgroundG = g.g().addClass('D1-background');

    loadImage({ src: D1background, key: 'D1background', type: 'svg' }).use().appendTo(backgroundG);
    //clipPath 설정
    const D1doorBorder = document.getElementById('D1doorBorder').getAttribute('points').split(' ');
    const D1doorBorderPathArr = D1doorBorder.reduce((p, c) => {
        c.split(',').forEach(el => p.push(el));
        return p;
    }, []);
    D1doorBorderPathArr.pop();
    const D1doorBorderPathData = convertToPathStr.polygon(D1doorBorderPathArr);
    clipPath.path(D1doorBorderPathData);

    // 오프닝, 엔딩 이미지 로드
    const starLoadImage = loadImage({ src: openingStart, key: 'D1OpeningStar', type: 'svg' });
    const leftDoorLoadImage = loadImage({ src: leftDoor, key: 'D1LeftDoor', type: 'svg' });
    const rightDoorLoadImage = loadImage({ src: rightDoor, key: 'D1RightDoor', type: 'svg' });

    // 오프닝, 엔딩 이미지 그리기
    const leftDoorImage = leftDoorLoadImage.use().transform('t25, 0').appendTo(leftDoorG);
    const rightDoorImage = rightDoorLoadImage.use().transform('t370, 0').appendTo(rightDoorG);
    const starImage = starLoadImage
        .use()
        .transform('t' + [400 - starLoadImage.width / 2, 250 - starLoadImage.height / 2])
        .appendTo(starG);

    // 오프닝, 엔딩 애니메이션

    const opening = async () => {
        await startAnim({ start: starImage, duration: 1500 });
        await doorAnim({ term, duration: 1500, leftDoor: leftDoorImage, rightDoor: rightDoorImage, type: 'open', easing: window.mina.bounce });
        await starAnim({ star: starImage, duration: 750 });
        await doorAnim({ term, duration: 500, leftDoor: leftDoorImage, rightDoor: rightDoorImage, type: 'close' });
        await wait(300);
    };

    const ending = async () => {
        await doorAnim({ term: 400, duration: 1500, leftDoor: leftDoorImage, rightDoor: rightDoorImage, type: 'open' });
    };

    return { opening, ending };
}

function doorAnim({ term, duration, leftDoor, rightDoor, type, easing }) {
    const { local: leftLocal } = leftDoor.transform();
    const { local: rightLocal } = rightDoor.transform();
    const sign = type === 'open' ? -1 : 1;
    return new Promise(resolve =>
        Snap.animate(
            0,
            1,
            val => {
                const leftDx = sign * val * term;
                const rightDx = -1 * sign * val * term;
                leftDoor.transform(leftLocal + 't' + [leftDx, 0]);
                rightDoor.transform(rightLocal + 't' + [rightDx, 0]);
            },
            duration,
            easing,
            resolve
        )
    );
}

function starAnim({ star, duration }) {
    const { local } = star.transform();
    return new Promise(resolve =>
        Snap.animate(
            0,
            1,
            val => star.transform(local + 's' + val + 'r' + val * 270).attr({ opacity: val }),
            duration,
            val => 1 - val,
            () => {
                star.remove();
                resolve();
            }
        )
    );
}
function startAnim({ start, duration }) {
    return new Promise(resolve => {
        Snap.animate(
            0,
            1,
            val => {
                start.attr({ opacity: val });
            },
            duration,
            val => {
                if (val <= 0.3) {
                    const { a, b } = getSimultaneousEquationConstant([0, 1], [0.3, 0.25]);
                    return a * val + b;
                }
                if (val <= 0.5) {
                    const { a, b } = getSimultaneousEquationConstant([0.3, 0.25], [0.5, 0.8]);
                    return a * val + b;
                }
                if (val <= 0.7) {
                    const { a, b } = getSimultaneousEquationConstant([0.5, 0.8], [0.7, 0.5]);
                    return a * val + b;
                }
                if (val <= 1) {
                    const { a, b } = getSimultaneousEquationConstant([0.7, 0.5], [1, 1]);
                    return a * val + b;
                }
            },
            resolve
        );
    });
}
